import React, { Component } from "react";
import PropTypes from "prop-types";
import st from "./style.js";

class SmallCard extends Component {
  
  constructor(props) {
    super(props);
    this.state = {};
  }

  onClick = () =>{
    this.props.onClick();
  }

  render() {
    return (
      <a style={{...st.container, ...this.props.style}} onClick={this.onClick} href={this.props.href}>
        <img src={this.props.img} style={st.image}/>
        <div>
          <div style={st.ctnLabel}>
            <div style={st.label}>{this.props.label}</div>    
          </div>
        </div>
      </a>
    );
  }
}

SmallCard.propTypes = {
  label: PropTypes.string,
  img: PropTypes.string,
  onClick:PropTypes.func,
  href:PropTypes.string,
  style: PropTypes.object,
};
SmallCard.defaultProps = {
  label: "",
  img: '',
  onClick:()=>{},
  href:"",
  style: {},
};

export default SmallCard;
